body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.devices-main {
  height: 1400px;
  margin-top: -1.3em;
  text-align: center; }
  .devices-main .devices-filters {
    background: #18afa3;
    height: 1400px;
    padding-left: 0 !important;
    padding-right: 0 !important;
    color: white; }
    .devices-main .devices-filters h4 {
      font: caption;
      text-align: left;
      margin-left: 5px; }
  .devices-main .filter-item {
    border-bottom: 1px solid white; }
  .devices-main .filter-body {
    text-align: left;
    padding-left: 20px; }
  .devices-main .devices-list {
    padding-left: 0 !important;
    padding-right: 0 !important; }
  .devices-main .list-about {
    color: gray; }
  .devices-main .list-button {
    margin-top: 3%;
    text-align: right; }
  .devices-main .list-search {
    height: 100px;
    border-bottom: 1px solid lightgray;
    display: table;
    width: 102%; }
    .devices-main .list-search .form-group {
      display: table-cell;
      vertical-align: middle; }
  .devices-main .search-item {
    margin: 20px 0 0 0;
    overflow: hidden; }
    .devices-main .search-item a {
      text-decoration: none;
      color: #333333; }
    .devices-main .search-item .description {
      text-align: left;
      margin-left: 4px; }
      .devices-main .search-item .description p {
        margin-bottom: 0 !important; }

h3 {
  margin-bottom: 1em; }

